.TodoItem {
    background-color: #fafafa;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    text-align: center;
    border-radius: 50px;
    box-shadow: 0 5px 50px rgba(32, 35, 41, .15)
}

.TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400
}

.TodoItem-p--complete {
    text-decoration: line-through
}

.Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: 700
}

.Icon-check {
    position: absolute;
    left: 12px;
    font-size: 1.8rem;
}

.Icon-check--active {
    color: #4caf50
}

.Icon-delete {
    position: absolute;
    margin: 10px;
    right: 0;
    color: #8A8D90;
}

.Icon-delete:hover {
    color: red
}